<template>
  <el-container>
    <el-header class="header">
      <div class="left">
        <div>
          <span>医生姓名：</span>
          <el-input class="com-input" placeholder="请输入医生名称" v-model="docName" />
          <span>医院职称：</span>
          <el-select v-model="gradeValue" class="com-input" placeholder="全部">
            <el-option v-for="item in hosGrade" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
          <span>医院名称/别名：</span>
          <el-input class="com-input" placeholder="请输入医院名称/别名" v-model="hosNickName" />
        </div>
        <div class="group">
          <span>医生手机：</span>
          <el-input class="com-input" placeholder="请输入医生手机号" v-model="mobile" />
          <span class="text">科室：</span>
          <el-select class="com-input" v-model="departValue" placeholder="选择一级科室">
            <el-option v-for="item in depart" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
          <el-select class="com-input" v-model="subDepartValue" placeholder="选择二级科室">
            <el-option v-for="item in subDepart" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
        </div>
        <div class="group">
          <span class="text">地区：</span>
          <user-address />
        </div>
      </div>
      <div class="right">
        <el-button type="primary" icon="el-icon-search" class="btn">查询</el-button>
        <el-button type="primary" @click="addCom" class="btn">新增医生</el-button>
      </div>
    </el-header>
  </el-container>
</template>
<script>
import { hosGrade, sectionInfo } from "../../../../data/hosNetwork";
import UserAddress from "../../../../components/address";
import _ from "lodash";
export default {
  name: "doctorOptions",
  data() {
    return {
      depart: [], //一级科室
      subDepart: [], //二级科室
      departValue: "",
      subDepartValue: "",
      hosGrade,
      docName: "", //医生姓名
      hosNickName: "", //医院别称
      mobile: "", //医生手机
      gradeValue: "",
    };
  },
  components: {
    UserAddress
  },
  methods: {
    addCom() {
      this.$router.push("addDoc?type=add");
    }
  },
  watch: {
    departValue: function(curVal, oldVal) {
        if (!curVal) return;
        let departs = _.filter(sectionInfo, (item, key) => {
          return item.value === curVal;
        });
        let subDepart = _.map(departs[0].children, (item, key) => {
          let obj = {};
          obj.value = item.value;
          obj.label = item.label;
          return obj;
        });
        this.subDepartValue = "";
        this.subDepart = subDepart;
    }
  }
};
</script>
<style lang="less" scoped>
@import url("./../../../../assets/less/color.less");
.header {
  display: flex;
  text-align: left;
  border-bottom: 1px solid @border-gray;
  height: inherit !important;
  padding-bottom: 20px;
  .group {
    margin-top: 20px;
    display: flex;
    align-items: center;
    .section {
      display: inline-block;
      text-indent: 4.5em;
    }
    .text {
      display: inline-block;
      text-indent: 2em;
    }
  }
  .com-input {
    width: 210px;
    margin-right: 20px;
  }
  .right {
    display: flex;
    align-items: center;
    .btn {
      margin-left: 20px;
    }
  }
}
</style>